import Vue from 'vue';
import {
  ValidationProvider, ValidationObserver, configure, extend,
} from 'vee-validate';
/* import {
  ValidationProvider, extend, configure, required,
} from 'vee-validate'; */
// import { required } from 'vee-validate/dist/rules';
import BootstrapVue from 'bootstrap-vue';
import PortalVue from 'portal-vue';
import App from './App.vue';
import router from './router';
import store from './store';
import './registerServiceWorker';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-vue/dist/bootstrap-vue.css';

// Disable fast-exit behaviour for all providers in the app
configure({ bails: false, events: 'blur' });

extend('required', {
  validate: value => !!value,
  message: 'The {_field_} field is required',
});

extend('between', {
  params: ['min', 'max'],
  validate(value, { min, max }) {
    return Number(value) >= min && Number(value) <= max;
  },
  message: 'The {_field_} field must be between {min} and {max}',
});

extend('min', {
  params: ['min'],
  validate(value, { min }) {
    return Number(value) >= min;
  },
  message: 'The {_field_} field must be higher than {min}',
});

Vue.use(BootstrapVue);
Vue.use(PortalVue);
// Vue.use(VeeValidate, { events: 'blur' });
Vue.component('ValidationObserver', ValidationObserver);
Vue.component('ValidationProvider', ValidationProvider);
Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: {
    App,
  },
  data: () => ({
    value: '',
  }),
  render: h => h(App),
}).$mount('#app');
